import Swiper from 'swiper';
import { Navigation } from 'swiper/modules';

function sliderScrollHead() {
  const myCustomSlider = document.querySelectorAll('.scroll-head__slider .swiper');
  const sections = document.querySelectorAll('.scroll-head__slider');

  for( let i = 0; i < myCustomSlider.length; i++ ) {
    
    myCustomSlider[i].classList.add('swiper-' + i);
    sections[i].classList.add('scroll-head__slider-' + i);
    new Swiper('.scroll-head__slider-' + i + ' .swiper-' + i, {
      modules: [Navigation],
      spaceBetween: 16,
      speed: 800,
      grabCursor: true,
      navigation: {
        nextEl: `.scroll-head__slider-${i} .swiper-button-next`,
        prevEl: `.scroll-head__slider-${i} .swiper-button-prev`,
      }
    });
  }
};
sliderScrollHead();