import Swiper from "swiper";
import { Navigation, Pagination, Thumbs } from "swiper/modules";

function sliderReviews() {
  let thumbs = new Swiper('.reviews__slider .reviews-swiper-thumbs', {
    modules: [Pagination, Navigation],
    allowTouchMove: false,
    centeredSlides: true,
    slidesPerView: 2,
    speed: 800,
    breakpoints: {
      1387: {
        slidesPerView: 7,
      }
    },
    pagination: {
      el: '.reviews__pagination .swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
    navigation: {
      nextEl: ".reviews__slider .swiper-button-next",
      prevEl: ".reviews__slider .swiper-button-prev",
    },
  });
  let main = new Swiper('.reviews__slider .reviews-swiper-main', {
    modules: [Pagination, Navigation, Thumbs],
    spaceBetween: 16,
    speed: 800,
    grabCursor: true,
    thumbs: {
      swiper: thumbs,
    },
    pagination: {
      el: '.reviews__pagination .swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
    navigation: {
      nextEl: ".reviews__slider .swiper-button-next",
      prevEl: ".reviews__slider .swiper-button-prev",
    },
    on: {
      slideChange: function (slide) {
        thumbs.slideTo(slide.activeIndex);
      },
    },
  });
}
sliderReviews();