
function scrollHead() {
    function isCenteredVertically(element) {
        let windowHeight = $(window).height();
        let scrollTop = $(window).scrollTop();
        let elementTop = $(element).offset().top;
        let elementHeight = $(element).outerHeight();
        let elementCenter = elementTop + elementHeight / 2;
        let viewportCenter = scrollTop + windowHeight / 2;
        return Math.abs(elementCenter - viewportCenter) < 200;
    }
    $(".scroll-head__slider").each(function() {
        let $this = $(this);
        function setActiveClass() {
            let shouldBeActive = isCenteredVertically($this);
            $this.toggleClass("active", shouldBeActive);
            if (shouldBeActive) {
                let dataHead = $this.attr("data-head");
                $(".reveal-head").removeClass("active");
                $(".reveal-head[data-head='" + dataHead + "']").addClass("active");
            }
        }
        setActiveClass();
        $(window).on('scroll', function() {
            setActiveClass();
        });
    });
}
scrollHead();